<template>
  <div class="container">
    <div class="row mt-4 mb-2">
      <div class="col-6 mx-auto">
        <label for="selects">Practice set</label>
        <select
          class="form-select"
          v-model="selectedset"
          name="selects"
          @change="changeset"
        >
          <option :value="select.id" v-for="select in selects" :key="select.id">
            {{ select.value }}
          </option>
        </select>
      </div>
    </div>
    <div class="row" v-if="wordstoplay.length">
      <div class="col-6 mx-auto border-end">
        <h5>Select pairs (click to select)</h5>
        <div
          class="form-check"
          v-for="word in wordstoplay"
          :key="word"
          @click.prevent="setword(word)"
        >
          <input
            class="form-check-input"
            type="checkbox"
            :value="word"
            :id="'flexCheckDefault_' + word"
            name="words"
            v-model="userans"
          />
          <label class="form-check-label" :for="'flexCheckDefault_' + word">
            {{ word }}
          </label>
        </div>
      </div>
      <div class="col-6 mx-auto">
        <h5>Your ans pairs (click to remove)</h5>
        <ul class="list-group list-group-flush">
          <li
            v-for="(ans, i) in userans"
            :key="ans"
            :class="i % 2 === 1 ? 'border-bottom' : ''"
            class="list-group-item listitems"
            @click="removeans(ans)"
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="16"
              height="16"
              fill="currentColor"
              class="bi bi-dash-circle-fill text-danger"
              viewBox="0 0 16 16"
            >
              <path
                d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zM4.5 7.5a.5.5 0 0 0 0 1h7a.5.5 0 0 0 0-1h-7z"
              />
            </svg>
            {{ ans }}
          </li>
        </ul>
      </div>
    </div>
    <div class="row my-1">
      <div v-show="showcorrectans" class="col-6 mx-auto">
        <h5>Correct pairs</h5>
        <ul class="list-group">
          <li class="list-group-item" v-for="(ans, i) in correctans" :key="i">
            {{ ans.join(", ") }}
          </li>
        </ul>
      </div>
    </div>
    <div class="row mt-4">
      <div class="col-6 mx-auto">
        <div class="d-grid gap-2 d-md-block">
          <button
            class="btn btn-primary mx-4"
            @click="check"
            type="button"
            v-if="wordstoplay.length"
          >
            Check ans
          </button>
          <button class="btn btn-secondary" @click="randomword" type="button">
            Random Pairs
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
const storagekey = "XATTA_WORDS_PAIR";
export default {
  data() {
    return {
      allwords: [],
      currentwords: [],
      selectedset: 0,
      selects: [
        { id: 0, value: "all words" },
        { id: 1, value: "set 1 " },
        { id: 2, value: "set 2 " },
        { id: 3, value: "set 3 " },
        { id: 4, value: "set 4 " },
        { id: 5, value: "set 5 " },
        { id: 6, value: "set 6 " },
        { id: 7, value: "set 7 " },
        { id: 8, value: "set 8 " },
        { id: 9, value: "set 9 " },
        { id: 10, value: "set 10 " },
        { id: 11, value: "set 11 " },
        { id: 12, value: "set 12 " },
        { id: 13, value: "set 13 " },
        { id: 14, value: "set 14 " },
        { id: 15, value: "set 15 " },
        { id: 16, value: "set 16 " },
        { id: 17, value: "set 17 " },
        { id: 18, value: "set 18 " },
        { id: 19, value: "set 19 " },
        { id: 20, value: "set 20 " },
        { id: 21, value: "set 21 " },
        { id: 22, value: "set 22 " },
        { id: 23, value: "set 23 " },
        { id: 24, value: "set 24 " },
        { id: 25, value: "set 25 " },
        { id: 26, value: "set 26 " },
        { id: 27, value: "set 27 " },
      ],
      wordstoplay: [],
      correctans: [],
      userans: [],
      showcorrectans: false,
      msg: "",
    };
  },
  mounted() {
    this.getwords();
  },
  computed: {},
  methods: {
    getwords() {
      axios
        .get(
          "https://raw.githubusercontent.com/Xatta-Trone/gregmatgrewordsdb/main/group_all.json"
        )
        .then((res) => {
          // console.log(res.data);
          this.allwords = res.data;
          this.currentwords = res.data;
          localStorage.setItem(storagekey, JSON.stringify(res.data));
        });
    },
    determineWords() {
      if (localStorage.getItem(storagekey)) {
        this.allwords = localStorage.getItem(storagekey);
        this.currentwords = localStorage.getItem(storagekey);
      } else {
        this.getwords();
      }
    },

    changeset() {
      console.log(this.selectedset);

      if (this.selectedset == 0) {
        this.currentWords = this.allwords;

        return;
      }

      let newset = this.allwords.filter((word) => {
        return word.group == this.selectedset;
      });

      if (newset.length > 0) {
        this.currentWords = newset;
        this.randomword();
      } else {
        this.$notify({
          group: "foo",
          text: "the set is not available!",
          position: "bottom",
          type: "error",
        });
      }

      console.log(newset);
    },
    check() {
      // divide words in two groups
      console.log(this.userans);
      let usertemparray = [];

      let total_correct_pair = this.correctans.length;
      let total_user_pair = Math.floor(this.userans.length / 2);
      let tempuserans = this.userans;
      console.log(total_correct_pair, total_user_pair);
      // make a sub array of usres array and check
      for (let i = 1; i <= total_user_pair; i++) {
        let temparray = tempuserans
          .slice((i - 1) * 2, (i - 1) * 2 + 2)
          .sort((a, b) => a.localeCompare(b));
        // console.log(temparray);
        let newarray = {
          ans: false,
          word: temparray,
        };

        usertemparray.push(newarray);
      }

      // console.log(usertemparray);

      // check if true
      usertemparray.forEach((singlepair) => {
        singlepair.ans = this.arrayAlreadyHasArray(
          this.correctans,
          singlepair.word
        );
        // console.log(
        //   "single pari",
        //   singlepair.word,
        //   this.arrayAlreadyHasArray(this.correctans, singlepair.word)
        // );
      });
      console.log(usertemparray);
      let items = document.getElementsByClassName("listitems");

      items.forEach((item, i) => {
        let indextocheck = Math.floor(i / 2);

        if (usertemparray.length > indextocheck) {
          item.className +=
            usertemparray[indextocheck].ans == true
              ? " list-group-item-success"
              : " list-group-item-danger";
        } else {
          item.className += " list-group-item-danger";
        }
      });
      this.showcorrectans = true;
    },

    arrayAlreadyHasArray(arr, subarr) {
      for (var i = 0; i < arr.length; i++) {
        let checker = false;
        for (var j = 0; j < arr[i].length; j++) {
          if (arr[i][j] === subarr[j]) {
            checker = true;
          } else {
            checker = false;
            break;
          }
        }
        if (checker) {
          return true;
        }
      }
      return false;
    },
    removeans(userans) {
      this.userans = this.userans.filter((word) => word != userans);
    },
    setword(word) {
      console.log(this.userans.includes(word));
      if (!this.userans.includes(word)) {
        this.userans.push(word);
      }

      // this.wordstoplay = this.wordstoplay.filter(function (value, index, arr) {
      //   return value != word;
      // });
    },

    randomword() {
      this.userans = [];
      this.showcorrectans = false;
      let pairs = Math.floor(Math.random() * 3) + 1;
      let tempwords = this.currentwords;
      let wordstoplaypairs = [];
      let correntans = [];
      for (let index = 0; index < pairs; index++) {
        let tempindex = Math.floor(Math.random() * tempwords.length);
        let pair = tempwords[tempindex];
        // normalize all words in one array
        let words = [...pair.synonyms, pair.word];
        // randomly select two words
        let selectedwords = words
          .sort(() => Math.random() - Math.random())
          .slice(0, 2)
          .sort((a, b) => a.localeCompare(b));
        //push it to the main array
        wordstoplaypairs.push(selectedwords);
        correntans.push(selectedwords);
        // console.log(tempwords.length);
        tempwords.splice(tempindex, 1);
        // console.log(tempwords.length);
        // console.log(pair, words, selectedwords);
      }

      //check if we need to populate again
      let wordstopopulate = (3 - pairs) * 2;
      // console.log(wordstopopulate);

      for (let index = 0; index < wordstopopulate; index++) {
        let tempindex = Math.floor(Math.random() * tempwords.length);
        let pair = tempwords[tempindex];
        // normalize all words in one array
        let words = [...pair.synonyms, pair.word];
        // randomly select two words
        let selectedwords = words
          .sort(() => Math.random() - Math.random())
          .slice(0, 1);
        //push it to the main array
        wordstoplaypairs.push(selectedwords);
        // console.log(tempwords.length);
        tempwords.splice(tempindex, 1);
        // console.log(tempwords.length);

        // console.log(pair, words, selectedwords);
      }

      //populate new words
      // console.log(pairs, wordstoplaypairs, correntans);
      // now populate to play
      this.correctans = correntans;
      this.wordstoplay = this.shuffle([].concat(...wordstoplaypairs));
    },

    shuffle(array) {
      for (let i = array.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * i);
        const temp = array[i];
        array[i] = array[j];
        array[j] = temp;
      }
      return array;
    },
  },
};
</script>

<style lang="css">
.form-check-input:checked:nth-child(0) {
  background-color: #000 !important;
  border: #000;
}
.class-1 {
  background-color: #000 !important;
  border: #000 !important;
}
.class-2 {
  background-color: #555 !important;
  border: #555 !important;
}
</style>
